@import url(https://fonts.googleapis.com/css?family=Ubuntu+Mono:400,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Ubuntu+Mono:400,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Ubuntu+Mono:400,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Ubuntu+Mono:400,700&display=swap);
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}

body{background:#f2e4da;color:#4f4b46}body .game-wrapper{display:flex;align-items:center;justify-content:center}body .game-wrapper .play-wrapper{align-self:flex-start}

.header{display:flex;align-items:center;justify-content:center;width:100%;height:90px;font-family:"Ubuntu Mono";font-size:40px;font-weight:bold;background:#d4bc44;margin-bottom:10vh}

.message{font-family:"Ubuntu Mono";font-size:22px;margin:20px 0;text-align:center;font-weight:bold}.hidden{visibility:hidden}.field-wrapper{width:30vw;height:30vw;margin:0 auto}.field-wrapper table{width:30vw;height:30vw;border-collapse:collapse;border:1px solid grey}.field-wrapper table .field-cell{border:1px solid grey}.field-wrapper table .blue{background:#525cba}.field-wrapper table .player-win{background:#73ba52}.field-wrapper table .machine-win{background:#ba5252}

.leaders-wrapper{margin-left:40px;font-family:"Ubuntu Mono";font-size:16px}.leaders-wrapper p{text-align:center;font-size:20px;font-weight:bold;text-transform:uppercase}.leaders-wrapper .leaders-table-wrapper{width:30vw;height:20vw;margin-top:20px;border-collapse:collapse;text-align:center}.leaders-wrapper .leaders-table-wrapper .leader-row-head{font-weight:bold;height:50px}.leaders-wrapper .leaders-table-wrapper td,.leaders-wrapper .leaders-table-wrapper th{border:3px solid #f2e4da;vertical-align:middle;border-left:none;border-right:none;background:#d9bdbd}

.settings-wrapper select,.settings-wrapper input,.settings-wrapper button{font-family:"Ubuntu Mono";font-size:16px;padding:5px;outline:none}.settings-wrapper select,.settings-wrapper input{margin-right:10px;background:#d9bdbd;border:1px solid #a68f8f}.settings-wrapper input{width:150px}.settings-wrapper button{width:150px;background:#9d87ab;border:1px solid #72617d;font-weight:bold}.settings-wrapper button:active{background:#b6a5c0}

